import { Grid, useTheme } from '@mui/material';
import PostCard from 'components/cards/PostCard';
import Layout from 'components/Layout';
import RowList from 'components/RowList';
import { Link, type PageProps } from 'gatsby';
import React, { useMemo } from 'react';

const BooksPage = ({ data }: PageProps<Queries.BooksPageQuery>) => {
  const theme = useTheme();
  const books = useMemo(() => data.allMarkdownRemark.edges.map(e => e.node), [data]);

  return (
    <Layout title={<Layout.Title>Books</Layout.Title>}>
      <RowList>
        {books.map(book => {
          const path = book.fields?.path ?? '/books/';
          const title = book.frontmatter?.title ?? '';
          const subTitle = book.frontmatter?.subTitle ?? '';
          const thumbnail = book.frontmatter?.thumbnail?.childImageSharp?.gatsbyImageData;
          const date = book.frontmatter?.date ?? '';

          return (
            <Grid
              item
              xs={12}
              key={book.id}
              sx={{
                padding: `${theme.spacing(0.25)} 0`,
                '& a': {
                  textDecoration: 'none',
                  display: 'block',
                  borderRadius: theme.shape.borderRadius * 2,
                  transition: 'background-color 0.2s ease-in-out',
                },
                '& a:hover': {
                  backgroundColor:
                    theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.01)' : 'rgba(255, 255, 255, 0.025)',
                },
                '& + &': {
                  borderTop: `1px solid ${theme.palette.divider}`,
                },
              }}
            >
              <Link to={path}>
                <PostCard title={title} subTitle={subTitle} thumbnail={thumbnail} date={date} />
              </Link>
            </Grid>
          );
        })}
      </RowList>
    </Layout>
  );
};

export default BooksPage;
